/* ? KONTENT - 1*/
.kontent-1 {
  /* border: 1px solid green; */
  width: 95vw;
  height: max-content;
  display: flex;
  margin-top: 20px;
}
.kontent-1 .box-logo {
  display: none;
  flex-direction: row;
  height: max-content;
  width: max-content;
}
.about {
  display: flex;
  flex-direction: column;
  height: max-content;
  margin-top: 100px;
  min-width: 624px;
  /* border: 1px solid green; */
}
.about h5 {
  font-family: "Roboto Mono", monospace;
  letter-spacing: 0.5px;
  font-size: 0.9rem;
  font-weight: 300;
}
.about h1 {
  font-size: 4rem;
  line-height: 3rem;
  color: #1e1e2f;
  margin-top: unset;
}
.about h3 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
  font-weight: 500;
  letter-spacing: normal;
  word-spacing: 3px;
  line-height: 1.5rem;
  color: #878a8f;
}
.about .box-kontak {
  display: flex;
  gap: 10px;
  font-size: 2rem;
  height: max-content;
}
.about a {
  color: #5e4a4a;
}
.about .cv-link {
  /* border: 1px solid #666666; */
  width: max-content;
  font-weight: 600;
  font-size: 1.2rem;
  padding: 7px 10px;
  display: flex;
  align-items: center;
}
.about a:hover {
  transition-duration: 0.3s;
}
.about .email-link:hover {
  color: #d23f47;
}
.about .whatsapp-link:hover {
  color: #25d366;
}
.about .instagram-link:hover {
  color: #e4405f;
}
.about .cv-link:hover {
  background-color: #666666;
  color: white;
}
.box-foto img {
  max-height: 100%;
}
.box-foto870 {
  display: none;
  margin: auto;
  height: 500px;
  width: 80%;
  margin: auto;
  /* border: 1px solid red; */
  background-image: url("../img/fotoku.PNG");
  background-size: cover;
}
.gradient-foto870 {
  /* border: 1px solid green; */
  width: 100%;
  height: 100%;
  background: transparent;
  background: linear-gradient(
    to top,
    rgba(255, 255, 255, 1) 2%,
    rgba(0, 0, 0, 0) 100%
  );
}

@media only screen and (max-width: 870px) {
  .kontent-1 {
    height: max-content;
    width: 90vw;
    /* margin: auto; */
    display: flex;
  }
  .kontent-1 .box-logo {
    margin-bottom: unset;
  }
  .about {
    /* border: 1px solid brown; */
    margin: unset;
    min-width: unset;
  }
  .about h5 {
    display: none;
  }
  .kontent-1 .box-foto {
    display: none;
  }
  .kontent-1 .box-foto870 {
    display: block;
  }
}
@media only screen and (max-width: 550px) {
  .about {
    margin: unset;
    width: 100vmax;
  }
  .kontent-1 .box-logo {
    display: flex;
  }
  .kontent-1 .box-foto870 {
    min-width: 92%;
    height: 60vh;
  }
}
@media only screen and (max-width: 350px) {
  .kontent-1 .box-foto870 {
    width: 100%;
  }
}
/* ! end KONTENT - 1*/

/* ? KONTENT - 2*/
.kontent-2 {
  /* border: 1px solid orangered; */
  /* width: 95vw; */
  margin: auto;
}
.kontent-2 h1 {
  font-size: 4rem;
  color: #1e1e2f;
  margin-top: 2em;
  margin-bottom: 0.5em;
}
.kontent-2 .box-project {
  display: flex;
  justify-content: center;
}
.kontent-2 ul {
  padding: unset;
  width: 100%;
  /* border: 1px solid blue; */
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(420px, 1fr));
  grid-gap: 30px;
}
.kontent-2 ul li {
  /* border: 1px solid grey; */
  position: relative;
  overflow: hidden;
}
.kontent-2 img.bg-project {
  width: 100%;
  height: 100%;
}

.kontent-2 .box-detail {
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
  position: absolute;
  padding: 7%;
  top: 0px;
  width: 86%;
  height: 90%;
  /* width: 100%;
  height: 100%; */
}
.kontent-2 .box-detail h1 {
}
.kontent-2 .box-detail h3 {
  font-size: 1.5rem;
}
.kontent-2 .box-detail h4,
.kontent-2 .box-detail h6 {
  margin: 0px;
}
.kontent-2 .box-detail a {
  color: black;
  text-decoration: none;
  font-size: 1rem;
  font-weight: 300;
}
.kontent-2 ul li img.bg2 {
  position: absolute;
  display: block;
  opacity: 0;
  top: 0;
  transform: scale(2);
}
.kontent-2 ul li:hover > img.bg2 {
  transition-duration: 0.3s;
  opacity: 1;
  transform: scale(1);
  /* display: block; */
}
/* .kontent-2 ul li:hover > img.bg-project {
  transition-duration: 0.3s;
  transform: scale(1.2);
} */
.kontent-2 ul li:hover > .box-detail, .kontent-2 ul li:hover > .box-detail a {
  transition-duration: 0.3s;
  color: white;
}
.kontent-2 ul li .box-detail a:hover {
  text-decoration: underline;
}

.kontent-2 ul li:hover > .box-detail {
  background-color: rgba(0, 0, 0, 0.7);
}

@media only screen and (max-width: 870px) {
  .kontent-2 {
    width: 90vw;
  }
  .kontent-2 img.bg-project {
    /* width: 100%; */
    /* border: 1px solid red; */
  }
}
@media only screen and (max-width: 420px) {
  .kontent-2 ul {
    grid-template-columns: unset;
  }
}
/* ! end KONTENT - 2*/

/* ? KONTENT - 3 */
.kontent-3 {
  width: 90vw;
  /* height: max-content; */
  margin-top: 70px;
  /* border: 1px solid red; */
  display: flex;
  flex-direction: column;
}
.kontent-3 h1 {
  /* font-size: 2rem; */
  color: #1e1e2f;
  margin-top: 2em;
}
.kontent-3 ul {
  padding: unset;
  width: 100%;
  /* border: 1px solid blue; */
  list-style: none;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(130px, 1fr));
  grid-gap: 10px;
}
.kontent-3 ul li {
  /* border: 1px solid green; */
  display: flex;
  border-radius: 10%;
  background-color: white;
  padding: 15px;
  height: 100px;
}
.kontent-3 ul li:hover {
  border: 1px solid rgba(0, 0, 0, 0.3);
}
.kontent-3 ul li img {
  width: 100%;
  height: 100%;
}
.kontent-3 ul li img.html-skill,
.kontent-3 ul li img.css-skill {
  /* border: 1px solid black; */
  width: 75%;
  margin: auto;
}
/* ! end KONTENT - 3 */
