/* ? BUTTON SCROLL TOP */
.btn-scrollTop {
  background-color: rgba(255, 255, 255, 0.5);
  position: fixed;
  bottom: 4vh;
  right: 6vw;
  /* right: 5vw; */
  z-index: 6;
  font-size: 2.2rem;
  border-radius: 50%;
  padding: 3px;
  display: flex;
  cursor: pointer;
  transition-duration: 0.5s;
}
.btn-scrollTop:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.btn-scrollTop:active {
  transition-duration: 0.1s;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
}
@media only screen and (max-width: 550px) {
  .btn-scrollTop {
    bottom: 13vh;
    transition-duration: 0.5s;
  }
}
/* ! end BUTTON SCROLL TOP */

/* ? BUTTON KONTAK */
.btn-mail {
  background-color: rgba(255, 255, 255, 0.5);
  display: flex;
  position: fixed;
  bottom: 4vh;
  left: 6vw;
  z-index: 6;
  font-size: 1.7rem;
  border-radius: 50%;
  padding: 10px;
  cursor: pointer;
  transition-duration: 0.5s;
  color: black;
  text-decoration: none;
}
.btn-mail:hover {
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.btn-mail:active {
  transition-duration: 0.1s;
  padding: 12px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
}
/* ! end BUTTON KONTAK */
