.kontent-menu-show {
  display: flex;
}
.kontent-menu-hide {
  display: none;
}
ul.kontent-menu {
  position: fixed;
  z-index: 10;
  margin: unset;
  top: 0px;
  left: 0px;
  width: 95vw;
  height: 100vh;
  list-style: none;
  /* display: flex; */
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  color: white;
}
ul.kontent-menu li {
  cursor: pointer;
}
ul.kontent-menu .close-menu {
  position: absolute;
  top: 3vmax;
  right: 5vmax;
  display: flex;
  padding: 10px;
  border-radius: 50%;
  border: 1px solid rgba(255, 255, 255, 0.5);
}
ul.kontent-menu li.menu-item {
  font-size: 4rem;
  padding: 0.1em;
}
