/* ?HEADER */
header,
header .box-logo,
.logo-title,
.box-menu {
  display: flex;
  /* border: 1px solid green; */
  height: max-content;
}
header {
  min-width: 94vw;
  padding: 10px;
  padding-right: 30px;
  align-items: center;
  justify-content: space-between;
}
.box-logo {
  float: left;
  align-items: center;
  gap: 10px;
}
.box-logo img.logo-img {
  width: 50px;
  height: 50px;
}
.box-logo div.logo-title {
  flex-direction: column;
  justify-content: center;
}
.box-logo .title-nama {
  font-size: 1.3rem;
  font-weight: 900;
}
.box-logo .title-status {
}
/* !END HEADER */

/* ?DISPLAY MENU */
.menu-dekstop {
  float: right;
  color: black;
  display: flex;
  padding: 10px;
  border-radius: 50%;
  cursor: pointer;
  border: 1px solid rgba(0, 0, 0, 0.5);
}
.menu-dekstop:hover {
}
.menu-mobile {
  display: flex;
  padding: 5px;
  border-radius: 50%;
  background-color: rgba(255, 255, 255, 0.5);
  font-size: 2.5rem;
  position: fixed;
  z-index: 7;
  right: 5vw;
  cursor: pointer;
  animation: menuMobileOof 0.5s forwards;
}
.menu-mobile:hover {
  /* border: 1px solid rgba(0, 0, 0, 0.3); */
  padding: 4px;
  background-color: rgba(0, 0, 0, 0.5);
  color: white;
}
.menu-mobile:active {
  transition-duration: 0.1s;
  border: none;
  padding: 5px;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
}
/* !END DISPLAY MENU */

/* ?HEADER scroll */
header.onTop {
  position: absolute;
  transition-duration: 1s;
}
header.onScroll {
  position: fixed;
  z-index: 7;
  top: -100px;
  background-color: rgba(255, 252, 252, 0.9);
  animation: scrollMenu 0.5s forwards;
}

@keyframes scrollMenu {
  0% {
    top: -100px;
  }
  100% {
    top: 0px;
  }
}
@keyframes menuMobileOof {
  0% {
    bottom: 4vh;
  }
  100% {
    bottom: -50vh;
  }
}
/* !END HEADER scroll */

/* ?RESPONSIVE HEADER */
@media only screen and (max-width: 550px) {
  header {
    display: none;
  }
  .menu-mobile {
    animation: menuMobileOn 0.5s forwards;
  }
}
@keyframes menuMobileOn {
  0% {
    bottom: -50vh;
  }
  100% {
    bottom: 4vh;
  }
}
/* !RESPONSIVE HEADER */
