/* ? KONTENT - 1*/
.kontent-1 {
  /* border: 1px solid green; */
  width: 95vw;
  height: max-content;
  display: flex;
  margin-top: 20px;
}
.kontent-1 .box-logo {
  display: none;
  flex-direction: row;
  height: max-content;
  width: max-content;
  margin-bottom: 2em;
}
.about {
  display: flex;
  flex-direction: column;
  height: max-content;
  margin-top: 100px;
  min-width: 624px;
  /* border: 1px solid green; */
}
.about h5 {
  font-family: "Roboto Mono", monospace;
  letter-spacing: 0.5px;
  font-size: 0.9rem;
  font-weight: 300;
}
.about h1 {
  font-size: 4rem;
  line-height: 3rem;
  color: #1e1e2f;
  margin-top: unset;
}
.about h3 {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 1.2em;
  font-weight: 500;
  letter-spacing: normal;
  word-spacing: 3px;
  line-height: 1.5rem;
  color: #878a8f;
}

@media only screen and (max-width: 870px) {
  .kontent-1 {
    height: max-content;
    width: 90vw;
    /* margin: auto; */
    display: flex;
  }
  .about {
    /* border: 1px solid brown; */
    margin: unset;
    min-width: unset;
  }
  .about h5 {
    display: none;
  }
}
@media only screen and (max-width: 550px) {
  .about {
    margin: unset;
    width: 100vmax;
  }
  .kontent-1 .box-logo {
    display: flex;
  }
}
@media only screen and (max-width: 350px) {
}
/* ! end KONTENT - 1*/
