/* ? KONTENT 1 */
footer * {
  margin: unset;
}
footer .kontent-1 {
  border-top: 1px solid rgba(0, 0, 0, 0.7);
  /* width: 90vw; */
  height: 40vh;
  display: flex;
  flex-direction: column;
  /* align-items: center; */
  justify-content: center;
  margin-top: 120px;
}
footer .kontent-1 h1,
footer .kontent-1 h4 {
  margin: 0.5em 0;
}
footer .kontent-1 h4 {
  font-weight: 600;
}
footer .kontent-1 .box-kontak {
  font-size: 2rem;
  height: max-content;
}
footer .kontent-1 .box-kontak a {
  color: #666666;
  margin-right: 0.5em;
}
footer .kontent-1 .kontent-1 a:hover {
  transition-duration: 0.3s;
}
footer .kontent-1 .kontent-1 .email-link:hover {
  color: #d23f47;
}
footer .kontent-1 .kontent-1 .whatsapp-link:hover {
  color: #25d366;
}
footer .kontent-1 .kontent-1 .instagram-link:hover {
  color: #e4405f;
}
/* ! end KONTENT 1 */
